export default {
  props: {
    visible: {
      type: Boolean,
      default: false,
      required: true,
    },
    title: {
      type: String,
      default: '提示',
    },
    placeholder: {
      type: String,
      default: '请输入内容',
    },
    mainColor: {
      type: String,
      default: '#e74a39',
    },
    defaultValue: {
      type: String,
    }
  },
  data() {
    return {
      value: '',
    }
  },
  mounted() {
    this.value = this.defaultValue
  },
  methods: {
    close() {
      this.$emit('update:visible', false)
    }
  }
}
